@import url("https://fonts.googleapis.com/css2?family=Fraunces:opsz,wght@9..144,700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    max-width: 100vw;
    min-height: 100vh;
    font-family: "Poppins", sans-serif;
}

.tour{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.tour-title{
    font-size: 3rem;
    margin: 6vh;
    border: 7px dashed rgb(1, 17, 160);
    border-radius: 20px;
    padding: 1vh 5vw;
    text-align: center;
    user-select: none;
}

.tourItem{
    max-width: 1300px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    /* align-items: center; */
    flex-wrap: wrap;
}

.card{
    width: 400px;
    height: -webkit-max-content;
    height: max-content;
    display: flex;
    flex-direction: column;
    box-shadow: 0 3px 8px rgba(0,0,0,.24);
    border-radius: 10px;
    align-items: center;
    margin: 1rem;
    padding: 1rem;
}

.imageDiv{
    width: 380px;
    overflow: hidden;
}

.card .image{
    aspect-ratio: 1/1;
    width: 100%;
    /* height: 350px; */
    object-fit: cover;
    transition: all 0.6s;
    -webkit-user-drag: none;
}

.card .image:hover{
    transform: scale(1.1);
    filter: grayscale(1);
}

.tour-details{
    margin: 20px 5px;
}

.tour-details .tour-price::before{
    content: "â‚¹ ";
}

.tour-details .tour-price{
    color: #1faa59;
    font-size: 1.3rem;
}

.tour-details .tour-name{
    font-size: 1.5rem;
}

.description span{
    color: #12b0e8;
    cursor: pointer;
}

.btn-red{
    cursor: pointer;
    padding: 10px 80px;
    border-radius: 10px;
    background-color: #b4161b21;
    border: 1px solid #b4161b;
    font-size: 1.1rem;
    font-weight: 700;
    margin-top: 18px;
    transition: all 0.3s;
}

.btn-red:hover{
    background-color: red;
    color: white;
}

.refresh{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.refresh h2{
    font-size: 3rem;

}

.refresh-button{
    padding: 10px 80px;
    border: 1px solid #fff;
    cursor: pointer;
    font-size: 1.1rem;
    transition: all 0.3s;
}

.refresh-button:hover{
    background-color: red;
    color: white;
}